import { memo, useState } from 'react';
import { Table } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { changeName, changeAge, changeCount } from './../store.js';

//   Cart    ,   Child   .
//  memo  child       ,      .
let Child = memo(function(){ // Child    
    return <div></div>
})

function Cart(){
    let state = useSelector((state)=>{ return state }) // ReduxStore  store   state 
    let dispatch = useDispatch(); // changeName   store.js   
    let [count, setCount] = useState(0);
    return (
        <div>
            <Child></Child>
            <button onClick={()=>{
                setCount(count+1)
            }}>+{count}</button>
            <h6> {state.user.age} {state.user.name} </h6>
            <button onClick={()=>{
                dispatch(changeAge(10));
            }}></button>
            <Table>
                <thead>
                    <tr>
                        <th>#</th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {
                        state.product.map(function(a,i){
                            let p = i;
                            return (
                                <SmallTable key={a.id} p={p}/>
                            )
                        })
                    }
                </tbody>
            </Table> 
        </div>
    )
}

function SmallTable({p}){
    let state = useSelector((state)=>{ return state })
    let dispatch = useDispatch(); // changeName   store.js   
    return (
        <tr>
            <td>{state.product[p].id}</td>
            <td>{state.product[p].name}</td>
            <td>{state.product[p].count}</td>
            <td><button onClick={()=>{
                dispatch(changeCount(state.product[p].id)); // dispatch    .
            }}>+</button></td>
        </tr>
    )
}

export default Cart;